@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
* {
  padding: 0;
  margin: 0;
  
}

body {
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dde8fc;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1280px;
}

p {
  line-height: 30px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New";
}
.app {
  display: flex;
  margin: 0 auto;
  max-width: 1280px;
  padding: 0 15px;
  min-height: 100vh;
  border-radius: 10px;
  flex-direction: column;
  background: linear-gradient(-45deg, red, gray);
    background-size: 400% 400%;
    -webkit-animation: gradient 15s ease infinite;
            animation: gradient 15s ease infinite;
}

@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}


@media (min-width: 800px) {
  .app {
    padding: 0 100px;
    margin-top: 5vh;
    min-height: 90vh;
  }
}


.home {
    flex-grow: 1;
    min-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .sub{
    color: black !important;
    text-transform: lowercase;
  }



  
  .home .title {
    width: 100%;
    font-size: 18px;
    text-align: center;
    font-family: "Righteous", cursive;
  }
  
  .home .title p {
    line-height: 50px;
    font-family: "Righteous", cursive;
    color: white;
  }
  
  .home .title button {
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 24px;
    margin-top: 30px;
    color: #ffffff;
    padding: 10px 15px;
    border-radius: 999px;
    background-color: black;
    font-family: "Righteous", cursive;

  }
  
  .home .person {
    width: 100%;
    margin-top: 20px;
  }
  
  .home .person img {
    width: 70%;
    border:2px solid #fff;
      box-shadow: 10px 10px 5px black;
      -moz-box-shadow: 10px 10px 5px black;
      -webkit-box-shadow: 10px 10px 5px black;
      -khtml-box-shadow: 10px 10px 5px black;
      display: flex;
      margin: 0 auto;
  }

  .button:hover{
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  
  @media (min-width: 800px) {
    .home {
      flex-direction: row;
      justify-content: space-between;
    }
    .home .title,
    .home .person {
      width: 50%;
      margin-top: 0;
      text-align: left;
      font-size: 25px;
    }
    .home .person img {
      width: 100%;
    }
  }
footer {

    width: 100%;
    padding: 10px 0;
    
  }
  
  footer img {
    max-width: 60px;
    margin: 0 auto;
  }
  
  footer img:not(:last-child) {
    margin: 0 auto
  }
.navbar {
    height: 100%;
    font-size: 1em;
  }
  
  .nav-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .logo {
    display: flex;
    max-width: 130px;
    height: 100%;
  }
  
  .logo img {
    width: 100%;
    margin-left: -18px;
  }
  
  .list-wrapper {
    display: flex;
    align-items: center;
  }
  
  .list-wrapper ul {
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    height: 86vh;
    left: -100vw;
    padding-top: 140px;
    text-align: center;
    position: absolute;
    transition: all 0.5s ease-in-out;
    background-color: rgb(0, 0, 0, 0.8);
  }
  
  .list-wrapper ul li {
    list-style: none;
    margin-bottom: 15px;
  }
  
  .list-wrapper ul li a {
    display: block;
    font-size: 24px;
    padding: 10px 0;
    color: #dddddd;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
  }

  .navi{
    font-size: 1em;
  }
  
  .list-wrapper ul li a:hover {
    color: #000000;
    background-color: #dddddd;
  }
  
  .list-wrapper img {
    opacity: 0;
    z-index: 4;
    width: 40px;
    right: 15px;
    padding: 8px;
    position: absolute;
    border-radius: 999px;
    background-color: #dddddd;
    transition: all 0.5s ease-in-out;
  }

  .link{
    font-size: 100px;
  }
  
  @media (min-width: 800px) {
    .list-wrapper img {
      display: none;
    }
  
    .list-wrapper ul {
      padding: 0;
      width: 100%;
      height: 100%;
      text-align: left;
      position: relative;
      background-color: transparent;
    }
  
    .list-wrapper ul li {
      display: inline-block;
      margin-bottom: 0;
    }
  
    .list-wrapper ul li:not(:last-child) {
      margin-right: 10px;
    }
  
    .list-wrapper ul li a {
      padding: 5px;
      color: #000000;
    }
  
    .list-wrapper ul li a:hover {
      padding: 5px;
      color: #333333;
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      background: transparent;
    }
  }
.about {
    flex-grow: 1;
  }
  
  .about h1 {
    margin-bottom: 20px;
  }
.skills {
    flex-grow: 1;
  }
  
  .skills h1 {
    margin-bottom: 20px;
  }
  
  .content {
    display: grid;
    font-size: 24px;
    grid-template-columns: 1fr 1fr;
  }
  
  .content div {
    margin-bottom: 25px;
  }
  
  .content strong {
    line-height: 25px;
    padding-bottom: 15px;
    display: block;
  }
  
  .content li {
    margin: 0;
    margin-bottom: 10px;
  }
  
  @media (min-width: 800px) {
    .content {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
.works {
    flex-grow: 1;
  }
  
  .works h1 {
    margin-bottom: 20px;
  }
.contact {
    flex-grow: 1;
  }
  
  .contact h1 {
    margin-bottom: 20px;
  }
  
  .contact iframe {
    border: 0;
    width: 100%;
    height: 100px;
    margin-bottom: 15px;
  }

  .cont{
      margin-bottom: 20px;
  }

  .img{
      width: 300px;
      height: 200px;
  }
